import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import Style from './style';

interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
	item: any;
}
/**
 * CategoryTile for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class CategoryTile extends React.PureComponent<Props> {
	componentDidMount() {}

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, item } = this.props;
		const { title, icon } = item;
		return (
			<div className={classes.flexBody}>
				<div>
					<span>
						<img src={icon} alt="icon" className={clsx(classes.icon)} />
					</span>
				</div>
				<div className={classes.title}>
					<span>{title}</span>
				</div>
			</div>
		);
	}
}
export default withStyles(Style, { name: 'DmartCorpFreshCategoryTile' })(CategoryTile);
